import {
    CELESTIAL_SIZE_MULTIPLIERS,
    CELESTIAL_SIZES,
    PLANET_NAMES,
    PLANET_TYPE_IMAGES,
    PLANET_TYPES
} from "../data/planetData";
import {extractRandomElement, getRandomElement, shuffle, speak} from "../misc/Utils";
import {ACTION_TYPE_EXPLORATION} from "../game/CelestialBody";
import {AwayMission} from "../game/AwayMission";
import {Universe} from "../game/Universe";
import {returnToScanner} from "../components/Dialog";
import * as Serialisation from "../misc/Serialisation";
import {CelestialBody} from "../game/CelestialBody";

export class Planet extends CelestialBody {
    constructor({traits = [], explorations = null, dangers: staticDangers = [], type, targetSize, ...config} = {}) {
        super(config);
        this.traits = traits;
        this.type = type;
        this.explorations = explorations;
        this.staticDangers = staticDangers;
        this.targetSize = targetSize;
    }

    initExplorations({explorations}) {
        console.assert(this.explorations === null);

        this.explorations = explorations;
    }

    notifyThatWasSelected() {
        super.notifyThatWasSelected();

        let awayMission = new AwayMission(this);
        awayMission.next();
    }

    get isFullyExplored() {
        return this.explorations.length === 0;
    }

    get disabled() {
        if (this.isFullyExplored) {
            return `Fully Explored`;
        }

        return false;
    }

    get actions() {
        if (!this.isFullyExplored) {
            return [
                {
                    name: `Land`,
                    action: () => this.notifyThatWasSelected(),
                    type: ACTION_TYPE_EXPLORATION,
                }
            ]
        } else {
            return []
        }
    }

    markExplored(exploration) {
        let index = this.explorations.indexOf(exploration);
        if (index !== -1) {
            this.explorations.splice(index, 1);
            Universe.notifyThatUniverseChanged();
        }
    }

    onFullyExplored() {
        speak(`${this.name} is now fully explored.`);
        returnToScanner();
    }
}

Serialisation.registerDynamicClass(Planet, "Planet");

export function placePlanets(universe, creator) {
    let planetNames = PLANET_NAMES.slice();
    shuffle(planetNames);

    let planetCandidates = [];
    for (let planetType of PLANET_TYPES) {
        for (let planetSize of CELESTIAL_SIZES) {
            for (let i = 0; i < 2; i++) {
                planetCandidates.push(new Planet({
                    id: `${planetType}${planetSize}${i}`,
                    name: planetNames.pop(),
                    targetSize: CELESTIAL_SIZE_MULTIPLIERS[planetSize],
                    image: PLANET_TYPE_IMAGES[planetType][planetSize],
                    type: planetType,
                    size: planetSize,
                }));
            }
        }
    }

    for (let i = 0; i < 25; i++) {
        let location = getRandomElement(universe.locations.filter(entity => entity.bodies.length < 3));
        let planet = extractRandomElement(planetCandidates);
        location.addBody(planet);
    }
}

export function removeEmptyPlanets(universe) {
    //remove empty planets
    for (let planet of universe.planets) {
        if (planet.explorations.length === 0) {
            planet.location.removeBody(planet);
        }
    }
}