import {ACTION_TYPE_CONFLICT, CelestialBody} from "../game/CelestialBody";
import alien_ship from "../data/planets/alien_ship.png";
import {CELESTIAL_SIZE_MEDIUM} from "../data/planetData";
import {getRandomElement, shuffle, speak} from "../misc/Utils";
import App from "../components/App";
import {returnToScanner} from "../components/Dialog";
import * as Serialisation from "../misc/Serialisation";
import React from "react";
import {SimpleLogExploration} from "./dataLogs";
import Exploration from "../game/Exploration";
import {Universe} from "../game/Universe";

export class AlienShipArrived extends Exploration {
    constructor(ship) {
        super();

        this.ship = ship;
    }

    calculate(mission) {
        let fromLocation = this.ship.location;
        fromLocation.removeBody(this.ship);

        let planet = mission.planet;
        let toLocation = planet.location;
        toLocation.addBody(this.ship);

        mission.possibleExplorations.push(new Hostilities({name: "Hostilities", combatants: "aliens", combatPower: 3}));
        mission.possibleExplorations.push(new Hostilities({name: "Hostilities", combatants: "aliens", combatPower: 3}));
        mission.possibleExplorations.push(new Hostilities({name: "Hostilities", combatants: "aliens", combatPower: 3}));
        shuffle(mission.possibleExplorations);

        return {
            title: `Ship in orbit!`,
            result: <div key="intro">
                <p>An alien ship from the {fromLocation.name} system, warped here (Move the token). It looks like it's sending some kind of pods towards the surface.</p>
            </div>,
            voice: `Alien ship arrived`
        }
    }
}

export class Hostilities extends Exploration {
    constructor({title = "Hostilities", combatants = "bugs", combatPower = 1, ...config}) {
        super(config);

        this.title = title;
        this.combatants = combatants;
        this.combatPower = combatPower;
    }

    calculate(mission) {
        return {
            title: this.title,
            result: <React.Fragment>
                <p>Your team was attacked by {this.combatants}, they have {this.combatPower} combat power. Perform a combat round with your away team.</p>
            </React.Fragment>,
            voice: `Danger, we are being attacked!`
        }
    }
}

Serialisation.registerDynamicClass(Hostilities, "Hostilities");


export function dynamicAlienShipDangers(planet) {
    let ret = [];

    for (let item of planet.location.bodies) {
        if (item instanceof AlienShip) {
            for (let i = 0; i < 3; i++) {
                ret.push(new Hostilities({name: "Hostilities", combatants: "aliens", combatPower: 3}));
            }
        }
    }

    for (let neighbouringLocation of planet.location.neighbours(Universe.theUniverse)) {
        for (let item of neighbouringLocation.bodies) {
            if (item instanceof AlienShip) {
                ret.push(new AlienShipArrived(item));
            }
        }
    }

    return ret;
}

export class AlienShip extends CelestialBody {
    constructor({attackDice, ...config}) {
        super({
            name: "Alien Ship",
            image: alien_ship,
            size: CELESTIAL_SIZE_MEDIUM,
            ...config,
        });

        this.attackDice = attackDice;
    }

    get disabled() {
        return false;
    }

    get actions() {
        return [
            {name: `Fight`, action: () => this.onAggression(), type: ACTION_TYPE_CONFLICT}
        ];
    }

    onAggression() {
        speak('They are locking weapons on us!');

        App.COMPONENT.setDialog(
            <div className="dialog">
                <div className="dialog-inner">
                    <div className="dialog-content">
                        <p>The alien ship attacks with {this.attackDice} {this.attackDice === 1 ? 'die' : 'dice'}.</p>
                        <p>Perform a standard combat round.</p>
                        <p>If the alien ship suffers at least 1 damage, the aliens are destroyed.</p>
                    </div>
                    <div className="dialog-bottom">
                        <button key="destroyed" onClick={
                            () => {
                                this.location.removeBody(this);

                                speak('Among the wreckage you found an artifact.');
                                App.COMPONENT.setDialog(
                                    <div className="dialog">
                                        <div className="dialog-inner">
                                            <div className="dialog-content">
                                                <p>Among the wreckage you found an artifact.</p> {/*TODO: Nice printing of this*/}
                                                <p>Remove the alien ship from the game.</p>
                                            </div>
                                            <div className="dialog-bottom">
                                                <button key="next" onClick={() => {
                                                    returnToScanner();
                                                }}>OK</button>
                                            </div>
                                        </div>
                                    </div>
                                );
                            }
                        }>Destroyed
                        </button>
                        <button key="notdestrotyed" onClick={() => {
                            speak('Alien ship remains intact');
                            returnToScanner();
                        }}>Not Destroyed
                        </button>
                    </div>
                </div>
            </div>
        );
    }
}

Serialisation.registerDynamicClass(AlienShip, "AlienShip");

export function placeAlienShips(universe) {
    //
    // Alien ship
    //
    let ships = [];
    for (let i = 0; i < 3; i++) {
        let location = getRandomElement(universe.locations.filter(entity => entity.bodies.length < 3));

        let ship = new AlienShip({
            id: `alienship-${i}`,
            attackDice: 2+i,
        });

        ships.push(ship);
        location.addBody(ship);
    }

    universe.dataLogs.push(new SimpleLogExploration({
        text: `You have found and decoded an alien communication device. Here is what we learned: the weakest alien ship was located at ${ships[0].location.name}, the strongest was at ${ships[2].location.name} and the remainig one was at ${ships[1].location.name}. All of them carry an artifact on board.`
    }));
}