function placeEntities(universe) {
    for (let mapEntityData of MAP_ENTITY_DATA) {
        universe.registerEntity(new Location(mapEntityData));
    }

    universe.registerEntity(new ScientistDataLogEntity());
}


export function createPossibleSourceFilters(universe, creator) {
    let possibleSourceFilters = [];

    PLANET_TYPES.forEach((planetType) => {
        possibleSourceFilters.push({
            description: `on ${planetType} planets`,
            filter: (planet) => planet.type === planetType
        });
    });

    CELESTIAL_SIZES.forEach((size) => {
        possibleSourceFilters.push({
            description: `on ${size} planets`,
            filter: (planet) => planet.size === size
        });
    });

    SECTORS.forEach((sector) => {
        possibleSourceFilters.push({
            description: `on planets in ${sector} sector`,
            filter: (planet) => planet.location.sector === sector
        });
    });

    [null, PLANET_TRAIT_GREEN_NEBULA, PLANET_TRAIT_PINK_NEBULA, PLANET_TRAIT_BLUE_NEBULA].forEach((nebula) => {
        possibleSourceFilters.push({
            description: nebula === null ? `on planets not in any nebula` : `on planets in ${nebula} nebula`,
            filter: (planet) => planet.location.nebula === nebula
        });
        possibleSourceFilters.push();
    });

    const totalMass = planets => {
        let mass = 0;
        for (let planet of planets) {
            mass += CELESTIAL_SIZE_MULTIPLIERS[planet.size];
        }
        return mass;
    }

    console.log("possibleSourceFilters", possibleSourceFilters.length);
    possibleSourceFilters = possibleSourceFilters.filter(sourceFilter => totalMass(universe.planets.filter(sourceFilter.filter)) > 6);
    console.log("possibleSourceFilters after viability test", possibleSourceFilters.length);

    shuffle(possibleSourceFilters);
    creator.possibleSources = possibleSourceFilters;
}

export function initialDialog(universe) {
    let spaceStationLocation = universe.entityForId(universe.startingLocationId);
    let spaceStation = spaceStationLocation.bodies[0];

    let location = `Albatross is located in ${spaceStationLocation.nameWithHints}. This is where all the players start their journey.`;
    speak(location);

    return <div className="dialog">
        <div className="dialog-inner">
            <div className="dialog-content">
                <img className="detailed-planet-image" src={spaceStation.image} alt={spaceStation.name} />
                <h1>{spaceStation.name}</h1>
                <p>Two days ago we completed a the deployment of this space station in this unexplored region of our galaxy.</p>
                <p>Your goal is to be the first to collect 4 artifacts.</p>
                <p><em>{location}</em></p>
            </div>
            <div className="dialog-bottom">
                <button onClick={() => returnToScanner()} >Start game</button>
            </div>
        </div>
    </div>
}


function fillEventsBasedOnCandidates(universe, creator) {
    creator.fillUniverse();
}

export function createDemoDynamicEvents(planet) {
    return [
        ...dynamicTravelDangers(planet),
        ...dynamicAsteroidDangers(planet),
        ...dynamicAlienShipDangers(planet),
        ...dynamicDustStormDangers(planet),
        ...dynamicToxicDangers(planet),
        ...dynamicWildlifeDangers(planet),
    ]
}

export function createDemoUniverse() {
    let universe = new Universe({name: chance.word()});
    let creator = new Creator(universe);

    Universe.theUniverse = universe;

    placeEntities(universe, creator);
    placeSpaceStation(universe, creator);
    placePlanets(universe, creator);
    placeAsteroids(universe, creator);
    placeBlackholes(universe, creator);
    placeWormholes(universe, creator);
    placeAlienShips(universe, creator);
    createPossibleSourceFilters(universe, creator);
    createResourceStashes(universe, creator);
    createLaserStash(universe, creator);
    fillStaticDustStorms(universe, creator);
    fillStaticToxicDangers(universe, creator);
    fillStaticWildlife(universe, creator);
    placeDataLogsOnPlanets(universe, creator);
    fillEventsBasedOnCandidates(universe, creator);
    removeEmptyPlanets(universe);

    Universe.theUniverse = null;

    return universe;
}