import {getRandomElement} from "../misc/Utils";
import {SimpleItem} from "../game/CelestialBody";
import black_hole_image from "../data/planets/black_hole.png";
import {CELESTIAL_SIZE_BIG} from "../data/planetData";


export function placeBlackholes(universe) {
    for (let i = 0; i < 2; i++) {
        let location = getRandomElement(universe.locations.filter(entity => entity.bodies.length < 3));

        let item = new SimpleItem({
            id: `blackhole-${i}`,
            name: "Black Hole",
            image: black_hole_image,
            size: CELESTIAL_SIZE_BIG,
        });

        location.addBody(item);
    }
}
