import Exploration from "../game/Exploration";
import React from "react";
import Icon from "../components/Icon";
import {CREW_TECHNICIAN, EQUIPMENT_LASER} from "../data/resourceData";
import {PLANET_TYPE_GRAY, PLANET_TYPE_GREEN, PLANET_TYPE_RED} from "../data/planetData";
import {getWeightedRandomElement} from "../misc/Utils";
import {EXPLORATION_CANDIDATE_PRIORITY_MUST} from "../game/Creator";
import {Hostilities} from "./alienShips";
import {SimpleLogExploration} from "./dataLogs";
import {ResourceExploration} from "./resources";

class WildlifeExploration extends Exploration {
    calculate(mission) {
        return {
            title: 'Giant bugs',
            voice: `Danger, giant bugs! Exhaust a crew member.`,
            result: <React.Fragment>
                <p>Your team was attacked by a swarm of giant bug like creatures!</p>
                <p>It turns out that the creatures are afraid of lasers <Icon obj={EQUIPMENT_LASER} />. If you have one - you are safe. If you don't, exhaust one of your crew members.</p>
            </React.Fragment>,
        }
    }
}

export function dynamicWildlifeDangers(planet) {
    const ret = [];

    if (planet.type === PLANET_TYPE_GRAY || planet.type === PLANET_TYPE_GREEN) {
        ret.push(new WildlifeExploration());
        ret.push(new WildlifeExploration());
    }

    return ret;
}

export function fillStaticWildlife(universe, creator) {
    universe.dataLogs.push(new SimpleLogExploration({
        text: `There are bugs living on gray and green planets, which are really afraid of laser beams.`
    }));
}
