import Exploration from "../game/Exploration";
import React from "react";
import Icon from "../components/Icon";
import {CREW_TECHNICIAN, EQUIPMENT_SUIT} from "../data/resourceData";
import {PLANET_TYPE_PINK, PLANET_TYPE_RED} from "../data/planetData";
import {SimpleLogExploration} from "./dataLogs";

class ToxicFumesExploration extends Exploration {
    calculate(mission) {
        return {
            title: 'Toxic fumes',
            result: <React.Fragment>
                <p>Planet atmosphere is not welcoming at all, the air is toxic!</p>
                <p>Exhaust each crew member not wearing an environmental suit <Icon obj={EQUIPMENT_SUIT} />.</p>
            </React.Fragment>,
            voice: `Danger, toxic fumes detected! Exhaust all crew members not wearing a suit.`
        }
    }
}


export function dynamicToxicDangers(planet) {
    const ret = [];

    if (planet.type === PLANET_TYPE_PINK) {
        ret.push(new ToxicFumesExploration());
        ret.push(new ToxicFumesExploration());
    }

    return ret;
}


export function fillStaticToxicDangers(universe, creator) {
    universe.dataLogs.push(new SimpleLogExploration({
        text: `Pink planets are toxic, using an environmental suit there is a good idea.`
    }));
}
