import React from "react";
import "./Icon.css";

export default class Icon extends React.Component {
    render() {
        let obj = this.props.obj;
        let count = this.props.count;
        let simple = typeof count !== 'number';

        if (simple) {
            return <React.Fragment>
                <span className="icon-inline"><img className="icon-img" src={obj.image} alt={obj.name} /></span>
            </React.Fragment>;
        } else {
            let text = count !== 1 ? obj.plural : obj.name;
            return <React.Fragment>
                {count} {text} <span className="icon-inline"><img className="icon-img" src={obj.image} alt={obj.name} /></span>
            </React.Fragment>;
        }
    }
}