import {ACTION_TYPE_MOVEMENT} from "../game/CelestialBody";
import wormholeImage from "../data/planets/wormhole.png";
import {Universe} from "../game/Universe";
import {getRandomElement, speak} from "../misc/Utils";
import App from "../components/App";
import {returnToScanner} from "../components/Dialog";
import * as Serialisation from "../misc/Serialisation";
import React from "react";
import {CelestialBody} from "../game/CelestialBody";

export class Wormhole extends CelestialBody {
    constructor({destinationId = null, ...config}) {
        super({image: wormholeImage, ...config});

        this.destinationId = destinationId;
        console.assert(this.destinationId !== null, this);
    }

    get destination() {
        return Universe.theUniverse.entityForId(this.destinationId);
    }

    get disabled() {
        return false;
    }

    get actions() {
        return [
            {name: `Enter`, action: () => this.onMove(), type: ACTION_TYPE_MOVEMENT}
        ];
    }

    onMove() {
        speak('The ship gets near the wormhole, are you sure you want to enter it?');

        App.COMPONENT.setDialog(
            <div className="dialog">
                <div className="dialog-inner">
                    <div className="dialog-content">
                        <p>{this.renderImage()}</p>
                        <p>The ship gets near the wormhole, time and space starts to bend, are you sure you want to send your move to enter it?</p>
                    </div>
                    <div className="dialog-bottom">
                        <button key="OK" onClick={
                            () => {
                                let text = `You arrive at ${this.destination.nameWithHints}.`;
                                speak(text);
                                App.COMPONENT.setDialog(
                                    <div className="dialog">
                                        <div className="dialog-inner">
                                            <div className="dialog-content">
                                                <p>{this.renderImage()}</p>
                                                <p>{text} Move your ship figure there.</p>
                                                <p>Disoriented, the crew is wondering if it's even possible to go back ...</p>
                                            </div>
                                            <div className="dialog-bottom">
                                                <button key="next" onClick={() => {
                                                    returnToScanner();
                                                }}>OK</button>
                                            </div>
                                        </div>
                                    </div>
                                );
                            }
                        }>Enter
                        </button>
                        <button key="back" className="not-important" onClick={() => {
                            returnToScanner();
                        }}>Leave
                        </button>
                    </div>
                </div>
            </div>
        );
    }
}

Serialisation.registerDynamicClass(Wormhole, "Wormhole");


export function placeWormholes(universe) {

    //
    // 2-way wormhole
    //
    let wormholeLocation1 = getRandomElement(universe.locations.filter(l => l.bodies.length < 3));
    let wormholeLocation2 = getRandomElement(universe.locations.filter(l => l.bodies.length < 3 && l !== wormholeLocation1));

    wormholeLocation1.addBody(new Wormhole({
        id: `wormhole-1`,
        name: `Wormhole C-61`,
        destinationId: wormholeLocation2.id
    }));

    wormholeLocation2.addBody(new Wormhole({
        id: `wormhole-2`,
        name: `Wormhole C-62`,
        destinationId: wormholeLocation1.id
    }));

    //
    // 1-way wormhole
    //
    getRandomElement(universe.locations.filter(l => l.bodies.length < 3 && l !== wormholeLocation1 && l !== wormholeLocation2)).addBody(new Wormhole({
        id: `wormhole-3`,
        name: `Wormhole C-63`,
        destinationId: getRandomElement(universe.locations.filter(l => l.bodies.length < 3)).id,
    }));
}