import * as Serialisation from "../misc/Serialisation";
import React from "react";
import {Entity} from "./Entity";
import "./Location.css";
import {Universe} from "../game/Universe";
import App from "../components/App"
import {speak} from "../misc/Utils";

export class Location extends Entity {
    constructor({neighbourIds = new Set(), sector = null, nebula = null, bodies = [], ...config}) {
        super(config);
        this.neighbourIds = neighbourIds;
        this.bodies = bodies;
        this.sector = sector;
        this.nebula = nebula;
    }

    neighbours(universe) {
        let ret = [];
        for (let neighbourId of this.neighbourIds) {
            let neighbour = universe.entityForId(neighbourId);
            console.assert(neighbour !== null, "neighbour !== null");
            ret.push(neighbour);
        }
        return ret;
    }

    removeBody(item) {
        let index = this.bodies.indexOf(item);
        console.assert(index !== -1);
        this.bodies.splice(index, 1);
    }

    addBody(item) {
        item.locationId = this.id;
        this.bodies.push(item);
    }

    get nameWithHints() {
        //const info = this.nebula ? `at a cross section of ${this.sector} sector and the ${this.nebula} nebula` : `in the ${this.sector} sector`;
        const info = `in the ${this.sector} sector`;

        return `the ${this.name} system ${info}`
    }

    onSelected() {
        speak(this.name);

        let neighbours = this.neighbours(Universe.theUniverse).map(neighbour => neighbour.name).join(", ").replaceLast(", ", " and ");

        //sorted bodies
        let bodies = this.bodies.sort((a, b) => b.actions.length - a.actions.length).map(body =>
            <div className={`celestial-info ${body.disabled ? ` disabled` : ``}`} key={body.id} >
                <div className={`celestial-view size-${body.size}`} style={{backgroundImage: `url(${body.image})`}} />
                <div className="celestial-actions">
                    {body.disabled ? body.disabled : null}
                    {body.actions.map(action =>
                        <button
                            key={action.name}
                            onClick={action.action}
                            className={`celestial-action action-${action.type} ${action.action ? "" : "celestial-action disabled"}`}>
                            {action.name}
                        </button>
                    )}
                </div>
            </div>

        );

        //{this.name} is fully explored, try flying to another location and exploring there.
        App.COMPONENT.setDialog(<div className="dialog-info">
            <div className="dialog-inner">
                <div className="dialog-content star-system-details">
                    <h1> {this.renderImage()} {this.name}</h1>
                    <p>{this.name} system is a system in the {this.sector} sector{this.nebula ? `, in the ${this.nebula} nebula` : `` }. It neighbours with {neighbours}.</p>
                    {bodies.length === 0 ? <p>No celestial bodies detected</p> : null}
                    {bodies}
                    <br style={{clear:"both"}}/>
                </div>
            </div>
        </div>, false);
    }
}

Serialisation.registerDynamicClass(Location, "Location");
