import React from "react";
import Exploration from "../game/Exploration";
import * as Serialization from "../misc/Serialisation";
import {StaticObject} from "../misc/Serialisation";
import Icon from "../components/Icon";
import {EQUIPMENT_LASER, RESOURCE_A, RESOURCE_B, RESOURCE_D} from "../data/resourceData";
import {SimpleLogExploration} from "./dataLogs";
import {getWeightedRandomElement} from "../misc/Utils";
import {EXPLORATION_CANDIDATE_PRIORITY_MUST} from "../game/Creator";
import {Hostilities} from "./alienShips";

export class Resource extends StaticObject {
    constructor({name, plural, image, isCrew = false, isEquipment = false, ...config}) {
        super(config);
        this.name = name;
        this.plural = plural;
        this.image = image;
        this.isCrew = isCrew;
        this.isEquipment = isEquipment;

        console.assert(typeof this.isCrew === "boolean");
        console.assert(typeof this.isEquipment === "boolean");
    }
}

export class ResourceExploration extends Exploration {
    constructor({title = null, resource, count = 1, ...config} = {}) {
        console.assert(typeof count === 'number', count);

        let providedResources = [];
        for (let i = 0; i < count; i++) {
            providedResources.push(resource);
        }

        super({
            providedResources: providedResources,
            ...config
        });

        this.resource = resource;
        this.count = count;
        this.title = title;
    }

    get actualTitle() {
        return this.title !== null ? this.title : (this.count === 1 ? this.resource.name : this.resource.plural);
    }

    calculate(mission) {
        return {
            title: this.actualTitle,
            result: <React.Fragment>
                <p>You found <Icon obj={this.resource} count={this.count} />.</p>
            </React.Fragment>,
            voice: `You found ${this.count} ${this.count === 1 ? this.resource.name : this.resource.plural}.`,
        }
    }
}

Serialization.registerDynamicClass(ResourceExploration, "ResourceExploration");

export function createResourceStashes(universe, creator) {
    for (let resource of [RESOURCE_A, RESOURCE_B]) {
        const filter1 = creator.possibleSources.pop();
        const filter2 = creator.possibleSources.pop();

        creator.addExplorationCandidates({
            exploration: new ResourceExploration({resource: resource, count: 1}),
            surfaceMinRatio: 0.1,
            minTotal: 15,
            maxTotal: 15,
            filter: (planet) => filter1.filter(planet) || filter2.filter(planet),
        });

        universe.dataLogs.push(new SimpleLogExploration({
            text: `${resource.plural} can be found ${filter1.description} and ${filter2.description}.`
        }));
    }

    let artifactFilter = creator.possibleSources.pop();

    creator.addExplorationCandidates({
        exploration: new ResourceExploration({resource: RESOURCE_D, count: 1}),
        surfaceMinRatio: 0.1,
        minTotal: 6,
        maxTotal: 8,
        filter: artifactFilter.filter,
    });

    universe.dataLogs.push(new SimpleLogExploration({
        text: `${RESOURCE_D.plural} can be found ${artifactFilter.description}.`
    }));
}

export function createLaserStash(universe, creator) {
    const STASH_SIZE = 2;
    const DEFENDERS = 2;
    let planet = getWeightedRandomElement(creator.planetsDistributionAccordingToFreeSpace()).planet;

    for (let i = 0; i < STASH_SIZE; i++) {
        creator.addExplorationCandidate({
            planet: planet,
            exploration: new ResourceExploration({title: "Hidden stash", resource: EQUIPMENT_LASER, count: 1}),
            priority: EXPLORATION_CANDIDATE_PRIORITY_MUST
        })
    }

    for (let i = 0; i < DEFENDERS; i++) {
        planet.staticDangers.push(new Hostilities({title: "Smugglers", combatants: "smugglers", combatPower: 1}));
    }

    universe.dataLogs.push(new SimpleLogExploration({
        text: `There are rumors that some smugglers stashed a bunch of laser cannons on a ${planet.type} planet in the ${planet.location.sector} sector.`
    }));
}



