import {getRandomElement} from "../misc/Utils";
import {CelestialBody} from "../game/CelestialBody";
import asteroids_image from "../data/planets/asteroids.png";
import {CELESTIAL_SIZE_SMALL} from "../data/planetData";
import * as Serialisation from "../misc/Serialisation";
import Exploration from "../game/Exploration";
import React from "react";
import Icon from "../components/Icon";
import {EQUIPMENT_SCANNER} from "../data/resourceData";
import {Universe} from "../game/Universe";

class MeteorExploration extends Exploration {
    calculate(mission) {
        return {
            title: 'Meteor',
            result: <React.Fragment>
                <p>A meteor hit the planet in your vicinity and covered the entire area in dust, you can not see anything and your away team is stranded.</p>
                <p>Exhaust a scanner <Icon obj={EQUIPMENT_SCANNER} /> to navigate your way through the dust, or exhaust your entire team.</p>
            </React.Fragment>,
            voice: `Danger, meteorites!`
        }
    }
}

export function dynamicAsteroidDangers(planet) {
    let ret = [];

    for (let item of planet.location.bodies) {
        if (item instanceof Asteroids) {
            ret.push(new MeteorExploration());
            ret.push(new MeteorExploration());
        }
    }

    for (let neighbouringLocation of planet.location.neighbours(Universe.theUniverse)) {
        for (let item of neighbouringLocation.bodies) {
            if (item instanceof Asteroids) {
                ret.push(new MeteorExploration());
            }
        }
    }

    return ret;
}

export class Asteroids extends CelestialBody {
    constructor({...config}) {
        super({
            name: "Asteroids",
            image: asteroids_image,
            size: CELESTIAL_SIZE_SMALL,
            ...config,
        });
    }
}

Serialisation.registerDynamicClass(Asteroids, "Asteroids");

export function placeAsteroids(universe) {
    for (let i = 0; i < 4; i++) {
        let location = getRandomElement(universe.locations.filter(entity => entity.bodies.length < 3));
        location.addBody(new Asteroids({id: `asteroid-${i}`}));
    }
}

